i.icon {
  display: inline-block;
  height: 13px;
  margin-right: 5px;
  width: 14px;
  &.user {
    background: img('/old/user.png', 'left no-repeat' ,''); }
  &.book {
    background: img('/old/book.png', 'left no-repeat' ,''); }
  &.settings {
    background: img('/old/settings.png', 'left no-repeat' ,''); }
  &.exit {
    background: img('/old/exit.png', 'left no-repeat' ,''); }
  &.tpls {
    background: img('/old/tpls.png', 'left no-repeat' ,''); }
  &.down {
    background: img('/old/down.png', 'left no-repeat' ,''); }
  &.loop {
    background: img('/old/loop.png', 'left no-repeat' ,''); }
  &.backlink, &.backarrow {
    background: img('/old/backlink-arrow.png', 'left no-repeat' ,''); }
  &.download {
    background: img('/old/download.png', 'no-repeat' ,'');
    display: inline-block;
    height: 13px;
    width: 14px; }
  &.dd-arrow {
    background: img('/old/dd-arrow.png', 'center no-repeat' ,'');
    position: absolute;
    right: 12px;
    top: 9px; }
  &.nav-arrow {
    background: img('/old/nav-arrow.png', 'center no-repeat' ,'');
    height: 10px; }
  &.edit {
    background: img('/old/edit-tpl.png', 'center no-repeat' ,''); }
  &.delete {
    background: img('/old/delete-tpl.png', 'center no-repeat' ,''); }
  &.tpl-remove {
    background: img('/old/tpl-remove.png', 'center no-repeat' ,'');
    height: 16px;
    width: 16px; }
  &.description {
    background: img('/old/description.png', 'center no-repeat' ,'');
    display: inline-block;
    height: 16px;
    vertical-align: middle;
    width: 16px; } }

